body {
  box-sizing: border-box;
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: 100%;
  font-family: "Gotham Pro", "Arial", sans-serif;

  @media (max-width: $mobile-width) {
    background-position: 0 50px;
  }
}

.no-bg {
  background: none;
}

.container {

  max-width: 979px;
  padding-left: 16px;
  padding-right: 16px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: $desktop-width) {
    width: 980px;
    padding: 0;
  }
}

h1 {
  font-size: 37px;
  line-height: 48px;
  margin-bottom: 0;

  @media (max-width: $mobile-width) {
    font-size: 27px;
    line-height: 35px;
  }
}

h2 {
  font-size: 32px;
  line-height: 40px;
  @media (max-width: $mobile-width) {
    font-size: 20px;
    line-height: 24px;
  }
}

/* Style the list */
ul.breadcrumb {
  padding: 10px 16px 10px 0;
  list-style: none;
}

/* Display list items side by side */
ul.breadcrumb li {
  display: inline;
  font-size: 12px;
  color: #828282;
}

/* Add a slash symbol (/) before/behind each list item */
ul.breadcrumb li+li:before {
  padding: 8px;
  color: #828282;
  //content: "\203A";
  content: "/\00a0";
}

/* Add a color to all links inside the list */
ul.breadcrumb li a {
  color: #0275d8;
  text-decoration: none;
}

/* Add a color on mouse-over */
ul.breadcrumb li a:hover {
  color: #828282;
  text-decoration: underline;
}

.delivery-how-order {
  @media (max-width: $mobile-width) {
    display: flex;
    flex-direction: column;;
  }
}
